.p{
    width: 30%;height: 40vh;
    margin: 20px 10px;
    border: 2px solid 
    rgb(243, 242, 242);
    border-radius: 10px 10px 
    0px 0px;
    overflow: hidden;
}
.pbrowser{
    height: 20px;
    background-color: rgb(243, 
    242, 242);
    display: flex;align-items: center;
    position: sticky;
    z-index: 2;
}
.pcircle{
    width: 6px;height: 6px;
    border-radius: 50%;
    background-color: white;
    margin: 3px;
}
.pimage{
    width: 100%;
    transition: all 10s ease;
}
.p:hover .pimage{
    transform: translateY(-100%);
}


@media screen and (max-width:480px) {
    .p{
        width:40%;height: 13vh;
    }
}