.c{
    height: 100vh;
   position: relative;
}
.cbg{
    width: 20px;height: 100%;
    background-color: green;
    position: absolute;
}
.crap{
    padding: 50px;display: flex;
}
.cleft{
    flex: 1;
}
.cright{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.ctitle{
    font-size: 60px;width: 80%;
}
.cinfoitem{
    display: flex;align-items: center;
    margin: 50px 0px;
    font-weight: 300;width: 70%;
    font-weight: bold;
    font-size: 20px;
}
.cicon{
    height: 30px;width: 30px;
    margin-right: 20px;
}
.cdes{
    font-weight: 400;
    font-size: 20px;
    
}

form{
    margin-top: 20px;
}
input{
    width: 50%;height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button{
    border: none;padding: 15px;
    background-color: green;
    font-weight: 500;
    cursor: pointer;
    font-size: 20px;
}


@media screen and (max-width: 480px) {
    .crap{
        flex-direction: column;
        padding: 0px 50px;
    }
    .ctitle{
        font-size: 30px;
    }
    .cinfoitem{
        width: 100%;
        margin: 20px 0px;
    }
    .cdes{
        display: none;
    }
}










